import React, { Component } from 'react'
import NewsItem from './NewsItem'
import Spinner from './spinner';
import PropTypes from 'prop-types'





export class News extends Component {
static defaultProps = {
    country : "in",
    pageSize: 9,
    category:"general" 
}
PropTypes={
    country : PropTypes.string,
    pageSize: PropTypes.number,
    category: PropTypes.string
}

  
  constructor(){
    super();
    console.log("i am a constructor");
    this.state={
             articles : [],
             page : 1 

    }
  }

    async componentDidMount(){
    let url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=dc6436da9b9d40738c4f68271bf2c4fe&pagesize=${this.props.pageSize}`;
    this.setState ({Loading : true}) 
    let data =  await fetch(url);

    let parsedData = await data.json()
    console.log(parsedData)

    this.setState({articles:parsedData.articles, 
      totalResults:parsedData.totalResults,
      Loading:false})

  }

  handlePreviousClick=async()=>{
    let url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=dc6436da9b9d40738c4f68271bf2c4fe&page=${this.state.page-1}&pagesize=${this.props.pageSize}`;
    this.setState ({Loading : true}) 
    let data =  await fetch(url);

    let parsedData = await data.json()
    this.setState ({Loading : false}) 

    this.setState({articles:parsedData.articles})
    this.setState({
        page: this.state.page - 1
    })      

  }
 handleNextClick= async()=>{
      if (!((this.state.page+1) >Math.ceil(this.state.totalResults/this.props.pageSize))){
        let url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=dc6436da9b9d40738c4f68271bf2c4fe&page=${this.state.page+1}&pagesize=${this.props.pageSize}`;
        this.setState ({Loading : true}) 
        let data =  await fetch(url);
         
        let parsedData = await data.json()
        this.setState ({Loading : false}) 

        this.setState({articles:parsedData.articles})
        this.setState({
            page: this.state.page + 1
            
        }) 
      }
      
  }

  render() {
    
    return (
      
      <div className="container my-3">
            <h2 className="text-center">NewsInfo --- Top HeadLines</h2>
            {this.state.Loading && <Spinner/>}
            
            <div className="row">
                  {!this.state.Loading && this.state.articles.map((element)=>{
                          return <div className="col-md-4" key={element.url}>
                
                          <NewsItem  title={element.title?element.title:""}  description={element.description?element.description:""} 
                          imageUrl={element.urlToImage} newsUrl={element.url} author={element.author} date={element.publishedAt}/>  
      
                        </div>
                  })}
            </div>
            
            <div className="container d-flex justify-content-between">

                <button disabled={this.state.page<=1}  type="button" className="btn btn-dark" onClick={this.handlePreviousClick}> &larr; previous</button>
                <button disabled={(this.state.page+1) >Math.ceil(this.state.totalResults/this.props.pageSize)} type="button" className="btn btn-dark" onClick={this.handleNextClick}>next &rarr;</button>

            </div> 
      </div>
    )
  }
}

export default News
